@import '../../App.scss';

.section3 {

    background-color: $white;
    position: relative;
    width: 100vw;
    padding-bottom: 80px;

    &-content {
        padding: 20px 12% 15px;
    }

    &-header-container {
        text-align: center;
        justify-content: center;
        align-items: center;
        margin: auto 0;
        padding: 20px;

        h1 {
            font-weight: 400;
            line-height: 2.5rem;
            margin: 0;
        }

        p {
            font-weight: 300;
            font-size: $font-med;
            margin: 0;
        }
    }

    /*&-content-container {
        display: flex;
        flex-direction: row;
        margin: auto 0;
        width: 100vw;
        padding: 15px 12% 0;
    }*/

    @media screen and (max-width: 425px) {
        &-header-container {
            padding: 0;
        }

        &-content {
            padding: 20px 6% 15px;
        }

    }

    @media screen and (max-width: 280px) {
        &-header-container {
            padding: 10px;
        }
    }
}